import {Reactive} from 'core/reactive';
import Ajax from 'core/ajax';
import Notification from 'core/notification';
import {default as Events} from 'mod_competvet/local/events';
import {UserSelectorMutations} from 'mod_competvet/local/user_selector_mutations';


/**
 * Main course editor module.
 *
 * All formats can register new components on this object to create new reactive
 * UI components that watch the current course state.
 *
 * @module     mod_competvet/local/user_list
 * @class     mod_competvet/local/userList
 * @copyright  2023 Laurent David <laurent@call-learning.fr>
 * @license    http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */
export class UserSelectorReactive extends Reactive {
    /**
     * Set up the user list
     *
     * This is called after we build the reactive instance to fill
     * the state with the initial data.
     *
     * @param {number} cmId cmid of the current course module
     * @param {string} roleArchetype role (student or teachers)
     */
    async loadUsers(cmId, roleArchetype) {
        this.cmId = cmId;

        try {
            const {users: userList} = await Ajax.call([{
                methodname: 'mod_competvet_get_user_list',
                args: {
                    cmid: this.cmId,
                    roletype: roleArchetype
                }
            }])[0];


            let currentUser = UserSelectorMutations.getCurrentUser(this.cmId);
            if (!currentUser || !userList.find((user) => user.id === currentUser)) {
                currentUser = 0;
                if (userList.length > 0) {
                    currentUser = userList[0].id;
                    UserSelectorMutations.setCurrentUser(currentUser, this.cmId);
                }
            }
            const initialState =
                {
                    info: {
                        currentUser: currentUser,
                        cmId: this.cmId,
                        filterShow: false,
                    },
                    users: userList,
                };
            this.setInitialState(initialState);
            return true;
        } catch (error) {
            await Notification.exception(error);
            return false;
        }
    }
}


/**
 * Trigger a global state changed event.
 *
 * @method dispatchStateChangedEvent
 * @param {object} detail the full state
 * @param {object} target the custom event target (document if none provided)
 */
export const dispatchStateChangedEvent = (detail, target) => {
    if (target === undefined) {
        target = document;
    }
    target.dispatchEvent(new CustomEvent(Events.userSelectorChanged, {
        bubbles: true,
        detail: detail,
    }));
};
